export default {
  name: 'infos',
  data() {
    return {
      visible: false,
      referenceDetail: {},
      link: '',
      isError: false,
      errorMessage: '',
    }
  },
  mounted() {
    this.getReferencePageDetail()
  },
  methods: {
    hide() {
      this.visible = false
    },
    show() {
      this.visible = true
    },
    copyText(id) {
      let referenceCode = document.getElementById(id)
      let selection = window.getSelection()
      let range = document.createRange()

      range.selectNodeContents(referenceCode)
      selection.removeAllRanges()
      selection.addRange(range)
      document.execCommand('Copy')
      selection.removeAllRanges()
    },
    updateLink(value) {
      this.link = value
    },
    async getReferencePageDetail() {
      try {
        let authHeaders = {
          Authorization: 'Bearer ' + this.$cookies.get('Token'),
        }
        let deneme = this.$cookies.get('Userkey')
        const response = await this.$axios.get(
          this.$config.FINANCE_API_URL +
            `/GetReferencePageDetail?Offset=0&RowSize=10`,
          {
            headers: authHeaders,
          }
        )

        if (response.data) {
          this.referenceDetail = response.data
        }
      } catch (e) {
        
      }
    },
    async updateUserLink(link) {
      
      try {
        let authHeaders = {
          Authorization: 'Bearer ' + this.$cookies.get('Token'),
        }
        let payload = {
          UserKey: link,
        }

        const response = await this.$axios.post(
          this.$config.FINANCE_API_URL + `/CreateReferenceURL`,
          payload,
          {
            headers: authHeaders,
          }
        )

        if (response.data) {
          
          if (response.data.Success === false) {
            this.errorMessage = response.data.Message
            this.isError = true
            setTimeout(() => {
              this.isError = false
            }, 3000)
          }
        }
      } catch (e) {
        const response = e.response
        if (response.data && response.data.ErrorCode === '99') {
          this.errorMessage = response.data.Message
          this.isError = true
          setTimeout(() => {
            this.isError = false
          }, 3000)
        }
      }
    },
  },
}
